<template>
   <section>
    <!-- Card View -->
        <div class="col-md-12 p-0">
          <loader :is-visible="isLoading"></loader>
          <div class="tab-content">
            <div id="step1" class="tab-pane call-detail fade p-0 border pb-3 active show">
              <section class="page-action w-100">
                <div class="container-fluid p-0">
                  <div class="col-md-12 p-0 mt-3">
                      <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 prof_control float-left" id="ulKeysContainer">
                        <div class="container-fluid p-0">
                          <div class="col-md-12 p-0">
                            <div class="row">
                              <Form v-slot="{ values, errors }" class="col-12 p-0" ref="form">
                              <div v-for="(item, allConfigDataIndex) in allConfigData"
                              class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6"
                              :key="'configdata-1' + allConfigDataIndex">
                              <div v-if="item.CORE_CONFIG_TYPE == 'dropdown' && item.CORE_CONFIG_KEY != 'TimeZone' && item.CORE_CONFIG_KEY != 'Currency'" class="form-group projectConfig">
                                  <label>
                                  <span> {{ $t(item.CORE_CONFIG_KEY) }}</span>
                                  <span class="text-danger">*</span>
                                  <a href="javascript:;" data-toggle="popoveruserguide" :data-content="item.CORE_CONFIG_DESC">
                                      <i class="fa fa-question-circle-o text-popover"></i>
                                  </a>
                                  </label>
                                  <Field :rules="{ 'required': true }" name="item.CORE_CONFIG_DESC" v-slot="{ errors }">
                                  <select :keys="item.CORE_CONFIG_ID" v-if="item.CORE_CONFIG_KEY != 'Currency'"
                                      class="select form-control" v-model="item.CORE_CONFIG_VALUE"
                                      name="EnableLocationTrackingModule">
                                      <option v-for="(items, GetoptionIndex) in Getoption(item.CORE_CONFIG_OPTIONS)"
                                      :value="items" :key="GetoptionIndex">
                                      {{ items }}
                                      </option>
                                  </select>
                                  <span name="EnableLocationTrackingModule" id="EnableLocationTrackingModule"
                                      class="invalid-feedback d-block">{{ errors[0] }}</span>
                                  </Field>
                              </div>
                              </div>
                              <div v-for="(item, allConfigIndex) in allConfigData"
                              class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6" :key="'configdata-2' + allConfigIndex">
                              <div v-if="item.CORE_CONFIG_TYPE == 'text' && item.CORE_CONFIG_KEY != 'TicketStartingNumber'" class="form-group projectConfig">
                                  <label>
                                  <span> {{ $t(item.CORE_CONFIG_KEY) }} </span>
                                  <span class="text-danger">*</span>
                                  <a href="javascript:;" :data-content="item.CORE_CONFIG_DESC" data-toggle="popoveruserguide">
                                      <i class="fa fa-question-circle-o text-popover" style="font-size: 14px"></i>
                                  </a>
                                  </label>
                                  <input :keys="item.CORE_CONFIG_ID" type="text" :name="item.CORE_CONFIG_DESC"
                                  :value="item.CORE_CONFIG_VALUE" class="textfield form-control" />
                                  <span name="LocationTracking" id="LocationTracking" class="invalid-feedback d-block">
                                  </span>
                              </div>
                              <div class="form-group" v-if="item.CORE_CONFIG_TYPE == 'text' && item.CORE_CONFIG_KEY == 'TicketStartingNumber'">
                                <label><span> {{ $t(item.CORE_CONFIG_KEY) }} </span><span class="text-danger">*</span>
                                  <a href="javascript:;" :data-content="item.CORE_CONFIG_DESC" data-toggle="popoveruserguide">
                                      <i class="fa fa-question-circle-o text-popover" style="font-size: 14px"></i>
                                  </a>
                                </label><br>
                                <div class="input-group">
                                  <input v-if="configureNumber==''" :keys="item.CORE_CONFIG_ID" type="text" :name="item.CORE_CONFIG_DESC" :value="item.CORE_CONFIG_VALUE" class="textfield form-control" />
                                  <input v-if="configureNumber!=''" :keys="item.CORE_CONFIG_ID" type="text" :name="item.CORE_CONFIG_DESC" :value="configureNumber" class="textfield form-control" />
                                  <div class="input-group-append">
                                    <span class="input-group-text">
                                        <a id="QuotePreviewtemplate" href="javascript:;" @click="ConfigureTicketNumber" value="" data-key="TicketStartingNumber">
                                            <i class="fa fa-pencil text-info font-18"></i>
                                        </a>
                                    </span>
                                  </div>
                                  <span name="LocationTracking" id="LocationTracking" class="invalid-feedback d-block"></span>
                                </div>
                              </div>
                              </div>
                              <div class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6">
                              </div>
                              <div class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6">
                              
                              </div>
                            
                            

                              <div class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6" id="radios" type="radiobutton"
                              v-for="(item, index) in allConfigData">
                              <label v-if="item.CORE_CONFIG_TYPE == 'radiobutton'">
                                  <span> {{ $t(item.CORE_CONFIG_KEY) }} </span>
                                  <span class="text-danger">*</span>
                                  <a href="javascript:;" data-toggle="popoveruserguide" :data-content="item.CORE_CONFIG_DESC"
                                  data-original-title="" title="">
                                  <i class="fa fa-question-circle-o text-popover" style="font-size: 14px;"></i>
                                  </a>
                              </label>
                              <div class="rad_value" type="radiobutton" :keys="item.CORE_CONFIG_ID"
                                  v-if="item.CORE_CONFIG_TYPE == 'radiobutton'">
                                  <label v-for="(items,indx) in Getoption(item.CORE_CONFIG_OPTIONS)"
                                  class="cogfiglbl form-check form-check-inline">
                                  <div class="custom-control custom-radio custom-control-inline">
                                      <input type="radio" class="custom-control-input" :name="item.CORE_CONFIG_DESC" :id="'rdo_' + indx+index" v-model="item.CORE_CONFIG_VALUE" :value="items" />
                                    <label class="custom-control-label" :for="'rdo_' + indx+index"></label>
                                  </div>{{ items == 'no' ? 'No' : 'Yes' }}
                                  </label>
                              </div>
                              </div>
                              <div class="col-sm-12 col-md-12 float-left col-lg-12 col-xl-12">
                              <div class="row flex-row-reverse">
                              <div class="col-lg-6 text-right">
                                  <a id="hlkSave" @click="SaveConfiguration(values, errors)" issecurityprompt="IsSecurityPrompt"
                                  class="btn btn-success formbtn" href="javascript:;" title="Save"><i
                                      class="fa fa-save pr-2"></i>Save</a>
                              </div>
                              </div>
                          </div>

                              </Form>
                            </div>
                          </div>
                        </div>
                      </div>
                  </div>
                </div>
              </section>
            </div>
          </div>
        </div>
        <modalConfigureTicketNumberVue v-if="showModalConfigureTicketNumber" @close="close"></modalConfigureTicketNumberVue>
    </section>
  </template>
  
  <script>
import modalConfigureTicketNumberVue from '../../components/Modal/modalConfigureTicketNumber.vue';
import DataService from "../../services/DataService";
  export default {
    components:{
        modalConfigureTicketNumberVue
    },
    data() {
      return {
        configureNumber:'',
        showModalConfigureTicketNumber:false,
        SizeDocStorage: null,
        RetentionValue: null,
        FileTypeVaule: "",
        SizeConfigDataId: null,
        RetentionConfigDataId: null,
        FileCongfigDataId: null,
        userStrogaeConfigId: null,
        clientStorageConfigId: null,
        SelectedValue: [],
        FileTypeValues: "",
        Options: [],
        FileTypeData: [],
        IsClientAutoActivated: '',
        ClientAutoActivationConfigId: null,
        lockRecord: [],
        roundingOff: [],
        timeFormat: [],
        defaultPageSize: [],
        EnablelocationTracking: [],
        locationTracking: null,
        timeZone: [],
        currency: [],
        getTimeZone: 0,
        getLocationTrackInterval: 0,
        paymentLink: '',
        getCurrencyValue: 0,
        getTimeFormatValue: 0,
        getTalygenTips: '',
        getPastRecordValue: 0,
        newlyAddUserValue: '',
        locationTrackingCoreConfigId: 0,
        defaultPageSizeValue: 0,
        newlyAddUserValueoption: [],
        talygenTipsOption: [],
        attachmentDiv: '',
        blobUrl: '',
        imgPath: '',
        containerName: '',
        arrayGetFileDetails: [],
        UserType: '',
        fileName: '',
        allConfigData: [],
        objData: [],
        timeZoneKeys: 0,
        allConfigObject: [],
        isLoading: false,
        defaultCompanyLogo: null,
        StorageAllocatedPerUser: null,
        StorageAllocatedPerClient: null,
        jsonObj: [],
        getCurrencyValueCoreConfigId: null,
        SelectValue: []
      };
    },
    computed: {},
    created: function () {
      this.GetConfigurationData();
      //this.GetTimeZone();
      //this.GetCurrency();
    },
    watch: function () {
      this.GetConfigurationData();
    },
    methods: {
      close:function(configureNumber){
        var vm=this;
        vm.showModalConfigureTicketNumber=false;
        vm.configureNumber=configureNumber;
      },
      ConfigureTicketNumber:function(){
        var vm=this;
        vm.showModalConfigureTicketNumber=true;
      },
      GetConfigurationData: function () {
        var vm = this;
        var myObj = `moduleId=80`
        DataService.GetConfigurationData(myObj).then(function (response) {
          vm.allConfigData = response.data.StorageKeys;
          vm.EnablelocationTracking = response.data.StorageKeys[0].CORE_CONFIG_OPTIONS.split("^");
          vm.locationTrackingCoreConfigId = response.data.StorageKeys[1].CORE_CONFIG_ID;
          vm.getTimeZones = response.data.StorageKeys[2].CORE_CONFIG_VALUE;
          vm.getTimeZoneCoreConfigId = response.data.StorageKeys[2].CORE_CONFIG_ID;
          vm.defaultPageSize = response.data.StorageKeys[3].CORE_CONFIG_OPTIONS.split("^");
          vm.getCurrencyValue = response.data.StorageKeys[7].CORE_CONFIG_VALUE;
          vm.getCurrencyValueCoreConfigId = response.data.StorageKeys[7].CORE_CONFIG_ID;
          vm.getTalygenTipsCoreConfigId = response.data.StorageKeys[9].CORE_CONFIG_ID;
          vm.lockRecord = response.data.StorageKeys[10].CORE_CONFIG_OPTIONS.split("^");
          vm.lockRecordCoreConfigId = response.data.StorageKeys[10].CORE_CONFIG_ID;
          vm.getPastRecordValue = response.data.StorageKeys[10].CORE_CONFIG_VALUE;
          vm.newlyAddUserValueoption = response.data.StorageKeys[11].CORE_CONFIG_OPTIONS.split("^");
          vm.newlyAddUserValue = response.data.StorageKeys[11].CORE_CONFIG_VALUE;
          vm.newlyAddUserValueCoreConfigId = response.data.StorageKeys[11].CORE_CONFIG_ID;
          vm.talygenTipsOption = response.data.StorageKeys[9].CORE_CONFIG_OPTIONS.split("^");
          vm.imgPath = response.data.StorageKeys[5].CORE_CONFIG_VALUE;
          vm.CompanyLogoCoreConfigId = response.data.StorageKeys[5].CORE_CONFIG_ID;
          vm.timeZoneKeys = response.data.StorageKeys[2].CORE_CONFIG_ID;
        })
      },
      SaveConfiguration: function (values, error) {
        var vm = this;
        vm.objData = [];
        vm.isLoading=true;
        var textValues = $('div#ulKeysContainer input:text,div#ulKeysContainer select,div#ulKeysContainer textarea,div#ulKeysContainer input:password'); //get all textboxes,textarea and drop down list from config list
        var checkBoxValues = $('div#ulKeysContainer div[type="checkbox"], div#ulKeysContainer div[type="radiobutton"]');
        for (var i = 0; i < textValues.length; i++) {
          if ($(textValues[i]).attr('keys') != undefined) {
            var textValue = $(textValues[i]).val().trim();
            if ($(textValues[i]).val().trim() == '') {
              if ($(textValues[i]).attr("class").indexOf("notrequired") == -1) {
                $(textValues[i]).addClass('is-invalid');
                $(textValues[i] + 'span#LocationTracking').html('Required');
              }
  
            }
            else {
              $(textValues[i]).removeClass('is-invalid');
            }
            let objStorageConfigData = {
              DataId: $(textValues[i]).attr('keys'),
              DataValue: textValue
            }
            vm.objData.push(objStorageConfigData);
          }
        }
        for (var i = 0; i < checkBoxValues.length; i++) {
          var getSelectedvalues = '';
          if ($(checkBoxValues[i]).attr('keys') != undefined && $(checkBoxValues[i]).attr('keys') != 'undefined') {
            if ($(checkBoxValues[i]).attr('type') == 'radiobutton') {
              var combinedName = ("config" + i);
              getSelectedvalues = $(checkBoxValues[i]).find('input:radio:checked').val();
            }
            let radioConfigDatas = {
              DataId: $(checkBoxValues[i]).attr('keys'),
              DataValue: getSelectedvalues
            }
            vm.objData.push(radioConfigDatas);
            let jsonObject = vm.objData.map(JSON.stringify);
            let uniqueSet = new Set(jsonObject);
            vm.allConfigObject = Array.from(uniqueSet).map(JSON.parse);
          }
        }
        var error = $('.is-invalid');
        let userset = [{
          refCode: 'TicketSetting',
          moduleName: 'Ticketing',
        }]
        var configData = "configData"
        var conditionData = "conditionData"
        var params = "[{" + JSON.stringify(configData) + ":" + JSON.stringify(vm.allConfigObject) + ", " + JSON.stringify(conditionData) + ":" + JSON.stringify(userset) + "}]"
        if ($(error).length <= 0) {
          DataService.ManageDocUserStorageSettingData(params).then((response) => {
            vm.isLoading = true;
            if (response.data == 1) {
              vm.ShowAlert(vm.$t('RecordsSavedSuccessfully'),'success');
              vm.isLoading = false;
            }
            else {
              vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t("Alert"));
              vm.isLoading = false;
            }
          });
        }
        else {
          vm.ShowAlert(vm.$t("PleaseCorrectHighlightedErrors"), "failure", true);
        }
      },
      Getoption: function (params) {
        params = params == null ? '' : params;
        var options = [];
        $.each(params.split('^'), function (i, v) {
          options.push(v);
        });
        return options;
      }
    }
  };
  </script>
  