<template>
    <div class="tab-pane call-detail fade p-0 border pb-3 active show">
            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 prof_control">
        <loader :is-visible="isLoading"></loader>
            <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
            v-on:OnSubmit="onSubmit"></dynamic-form>
            </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props:{
        CompanyType:{
            type: String,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Priority Name'),
                            name: "ResourceTypeName",
                            value: "",
                            placeholder: this.$t('Enter Priority Name'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            config: {
                                options: [{ value: "1001", name: "Active" }, { value: "1002", name: "InActive" }]
                            },
                            placeholder: this.$t('SelectStatus'),
                            validationRules: { "required": true, max: 100 },
                        },
                    ],
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "ColorField",
                            label: this.$t('ColorField'),
                            name: "ColorField",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Display Order'),
                            name: "Status",
                            value: "",
                            config: {
                                options: [{ value: "1", name: "1" }, { value: "2", name: "2" },{ value: "3", name: "3" }, { value: "4", name: "4" },{ value: "5", name: "5" }, { value: "6", name: "6" },{ value: "7", name: "7" },{ value: "8", name: "8" }]
                            },
                            placeholder: this.$t('SelectStatus'),
                            validationRules: { "required": true, max: 100 },
                        },
                    ],
                }
            ],
            buttons: [
                {
                    type: "button",
                    class: "popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4",
                    text: "Skip",
                    isVisible:this.CompanyType=='normal'?false:true,
                    onClick:this.Skip
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    isVisible:this.CompanyType=='normal'?true:false,
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    isVisible:this.CompanyType=='normal'?false:true,
                    text: "<i class='fa fa-save pr-2'></i>Save And Next",
                }
            ],
            ListData: []
        }
    },
    methods: {
        onSubmit(formfields, val) {
            var vm = this;
            var params = `priorityId=0&priorityName=${vm.FormSchema[0].Data[0].value}&statusId=${vm.FormSchema[0].Data[1].value}&displayOrder=${vm.FormSchema[1].Data[1].value}&priorityColor=${vm.FormSchema[1].Data[0].value}`
            DataServices.AddTicketPriority(params).then((response) => {
              if(response.data=="1") {
                vm.ShowAlert("Priority successfully added.","success");
                vm.$parent.ShowNextTab(2);
                vm.$parent.GetStorageSetupStatusDetail();
              }
              else{
                vm.ShowAlert(vm.$t('AddedError'),"failure");
              }
            });
        },
        Skip:function(){
            var vm=this;
            vm.$parent.ShowNextTab(2);
        }
    }
}
</script>