<script>
import DataService from '../../services/DataService';
import modalAdvanceSettingVue from './modalAdvanceSetting.vue';
import SetupTicketingVue from './SetupTicketing.vue';
export default {
components:{
    modalAdvanceSettingVue,
    SetupTicketingVue
},
data(){
    return{
        ShowManageView:false,
        showSetupTicketingPopup:false,
        SetupProgress:0,
        CompanyType:'normal'
    }
},
created:function(){
    var vm=this;
    vm.CheckLocateOrNonLocate();
    setTimeout(function () {
        vm.GetStorageSetupStatusDetail();
    }, 1000);
},
methods:{
    ShowSetTicketingPopup:function(){
        var vm=this;
        vm.showSetupTicketingPopup=true;
    },
    CheckLocateOrNonLocate:function(){
            debugger
            var vm=this;
            DataService.IsExtraMapFeature().then(function (response) {
                vm.CompanyType=response.data;
            });
    },
    GetStorageSetupStatusDetail: function () {
            debugger
            var vm = this;
            var url=`companyType=${vm.CompanyType}`
            DataService.GetStorageSetupStatusDetail(url).then(function (response) {
                if (response.data != null) {
                    var res = JSON.parse(response.data);
                    vm.SetupProgress = parseInt(res.data[0].TOTAL_PROGRESS);
                    if(vm.windowHeight<568){
                         if(vm.SetupProgress==25)
                        {
                            vm.progressColor="progress-bar progress-bar-striped bg-danger"
                        }
                        else if(vm.SetupProgress==50){
                            vm.progressColor="progress-bar progress-bar-striped bg-warning"
                        }
                        else if(vm.SetupProgress==75){
                            vm.progressColor="progress-bar progress-bar-striped bg-info"
                        }
                        else if(vm.SetupProgress==100){
                            vm.progressColor="progress-bar progress-bar-striped bg-success"
                        }
                    }
                    else{
                    if(vm.SetupProgress==25){
                        vm.progressColor='red'
                    }
                    else if(vm.SetupProgress==50)
                    {
                        vm.progressColor='yellow'
                    }
                    else if(vm.SetupProgress==75)
                    {
                        vm.progressColor='blue'
                    }
                    else if(vm.SetupProgress==100)
                    {
                        vm.progressColor='green'
                    }
                    }
                }
            });
        },
    showadvancesettingpopup:function(){
        var vm=this;
        vm.ShowManageView = true;
    },
    close:function(){
        var vm=this;
        vm.ShowManageView = false;
        vm.showSetupTicketingPopup=false;
    },
    RedirectToLinks: function(redirectType){
        var envLink=process.env;                
        if(redirectType=="FormBuilder")
        {
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/FormBuilder/Index'
            window.open(link);
            return false
        }
        else if(redirectType=="MapConfiguration")
        {
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Ticketing/MapConfig'
            window.open(link);
            return false
        }
        else if(redirectType=="MapExtent")
        {
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Ticketing/MapExtend'
            window.open(link);
            return false
        }
        else if(redirectType=="RuleEngine")
        {
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/RuleEngine/Index'
            window.open(link);
            return false
        }
        else if(redirectType=="ServiceCrew")
        {
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Ticketing/crew'
            window.open(link);
            return false
        }
        else if(redirectType=="TransformPending")
        {
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Ticketing/TransformPending'
            window.open(link);
            return false
        }
        else if(redirectType=="WorkType")
        {
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Ticketing/WorkTypeListing'
            window.open(link);
            return false
        }
        else if(redirectType=="Zone")
        {
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Ticketing/Zone'
            window.open(link);
            return false
        }
        else if(redirectType=="TicketGroup")
        {
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Ticketing/Group'
            window.open(link);
            return false
        }
        else if(redirectType=="Chain")
        {
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Ticketing/Chain'
            window.open(link);
            return false
        }
        else if(redirectType=="Chain")
        {
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Ticketing/Chain'
            window.open(link);
            return false
        }
        else if(redirectType=="IncidentType")
        {
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Ticketing/IncidentTypeListing'
            window.open(link);
            return false
        }
        else if(redirectType=="Canned"){
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/ReplyTemplate/Index'
            window.open(link);
            return false
        }
        else if(redirectType=="Layout"){
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Ticketing/ManageLayout'
            window.open(link);
            return false
        }
        else if(redirectType=="MapStatus"){
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Ticketing/MapStatus'
            window.open(link);
            return false
        }
        else if(redirectType=="ResourceType"){
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Ticketing/ResourceType'
            window.open(link);
            return false
        }
        else if(redirectType=="TransformPending"){
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Ticketing/TransformPending'
            window.open(link);
            return false
        }
    },
}
}
</script>
<style>
.c100.green .bar, .c100.green .fill {
    border-color: #8BC34A !important;
}

